












































































































import Vue from 'vue';

export default Vue.extend({
  name: 'Nav',
  components: {},
  computed: {},
  data: () => ({}),
  methods: {
    forceScroll(hash) {
      if (`#${hash}` !== window.location.hash) {
        return;
      }

      const element = document.getElementById(hash);

      if (element) {
        element.scrollIntoView();
      }
    },
  },
});
