





















































































































































































import Vue from 'vue';

export default Vue.extend({
  name: 'HardSkills',
  computed: {
    slidesLength() {
      return Object.keys(this.skills).length - 1;
    },
  },
  data: () => ({
    graphSettings: {
      autoDrawDuration: 600,
      color: '#c76ae6',
      gradient: ['#c76ae6', '#36e8c7'],
      padding: '24',
      height: 100,
      labelSize: '5',
    },
    skills: {
      design: {
        ['Figma']: 2,
        ['Illustrator']: 10,
        ['InDesign']: 4,
        ['Photography']: 8,
        ['Photoshop']: 9,
        ['Traditional Media']: 8,
      },
      development: {
        specific: {
          ['AngularJS']: 4,
          ['Git']: 4,
          ['Laravel']: 2,
          ['Lit']: 1,
          ['MongoDB']: 1,
          ['MySQL']: 3,
          ['NodeJS']: 3,
          ['Polymer']: 2.5,
          ['Postgres']: 2,
          ['React']: 1,
          ['Typescript']: 1,
          ['Vue']: 1.5,
        },
        languages: {
          ['C++']: 2,
          ['CSS']: 6,
          ['HTML']: 6,
          ['Javascript']: 5,
          ['NoSQL']: 1,
          ['PHP']: 4,
          ['Ruby']: 0.5,
          ['SCSS']: 4,
          ['SQL']: 3,
        },
      },
      digitalMedia: {
        ['After Effects']: 4,
        ['AuditionCC']: 4,
        ['Cinematography']: 5,
        ['FLStudio']: 2,
        ['Premiere Pro']: 8,
        ['ProTools']: 3,
      },
    },
    currentSlide: 0,
    canTransitionNext: true,
    slide: 0,
  }),
  methods: {
    afterLeave() {
      this.currentSlide = this.slide;
    },
    afterEnter() {
      this.canTransitionNext = true;
    },
    showNext() {
      if (!this.canTransitionNext) {
        return;
      }

      this.canTransitionNext = false;

      this.slide =
        this.currentSlide === this.slidesLength ? 0 : this.currentSlide + 1;
    },
    showPrevious() {
      if (!this.canTransitionNext) {
        return;
      }

      this.canTransitionNext = false;

      this.slide =
        this.currentSlide === 0 ? this.slidesLength : this.currentSlide - 1;
    },
  },
});
